import { Injectable } from '@angular/core';

@Injectable()
export class ResultService {

  //answers
  public aptituteAns=[];
  resoningAns=[];
  englisgAns=[];
  genralAns=[];
  computerAns=[];

  //option selected
  selectedA=[];
  selectedE=[];
  selectedR=[];
  selectedG=[];
  selectedC=[];

  constructor() { }

  setAptiAns(aptiAns){
    this.aptituteAns.push(aptiAns);
  }

  setResoningAns(rAns){
    this.resoningAns.push(rAns);
  }
  setEnglishAns(eAns){
    this.englisgAns.push(eAns);
  }

  setGenralAns(gAns){
    this.genralAns.push(gAns);
  }

  setComputerAns(cAns){
    this.computerAns.push(cAns);
  }

 setAOption(aopt){
   this.selectedA = aopt;
 }

 setROption(ropt){
   this.selectedR = ropt;
 }

 setEOption(eopt){
   this.selectedE = eopt;
 }

 setGOption(gopt){
   this.selectedG = gopt;
 }

 setCOption(copt){
   this.selectedC = copt;
 }

 clearAll(){
   this.aptituteAns=[];
   this.computerAns=[];
   this.englisgAns=[];
   this.resoningAns=[];
   this.genralAns=[];
 }
}



