import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import {RouterModule, Routes} from '@angular/router';

//service worker
import { ServiceWorkerModule } from '@angular/service-worker';
import { environment } from '../environments/environment';

//services
import { AuthService } from './services/auth.service';
import { QuestionService } from './services/question.service';
import { ExamService } from './services/exam.service';
import { ResultService } from './services/result.service';
//components
import { AppComponent } from './app.component';
import { NavbarComponent } from './components/navbar/navbar.component';
import { HomeComponent } from './components/home/home.component';
import { FooterComponent } from './components/footer/footer.component';
import { ContactComponent } from './components/contact/contact.component';
import { ServicesComponent } from './components/services/services.component';
import { FaqComponent } from './components/faq/faq.component';
import { UhomeComponent } from './components/uhome/uhome.component';
import { RegisterComponent } from './components/register/register.component';
import { LoginComponent } from './components/login/login.component';
import { FpasswordComponent } from './components/fpassword/fpassword.component';
import { UnavbarComponent } from './components/uhome/unavbar/unavbar.component';
import { UdashboardComponent } from './components/uhome/udashboard/udashboard.component';
import { TutorialsComponent } from './components/uhome/tutorials/tutorials.component';
import { QuestionsComponent } from './components/uhome/questions/questions.component';
import { ExamsComponent } from './components/uhome/exams/exams.component';
import { UstatusComponent } from './components/uhome/ustatus/ustatus.component';
import { AhomeComponent } from './components/ahome/ahome.component';
import { AnavbarComponent } from './components/ahome/anavbar/anavbar.component';
import { UploadComponent } from './components/ahome/upload/upload.component';
import { AloginComponent } from './components/alogin/alogin.component';
import { AdashboardComponent } from './components/ahome/adashboard/adashboard.component';
import { ExtraComponent } from './components/ahome/extra/extra.component';
import { TestboardComponent } from './components/testboard/testboard.component';
import { IntropageComponent } from './components/testboard/intropage/intropage.component';
import { MainpageComponent } from './components/testboard/mainpage/mainpage.component';
import { ResultpageComponent } from './components/testboard/resultpage/resultpage.component';
import { ExamboardComponent } from './components/examboard/examboard.component';
import { ExamintroComponent } from './components/examboard/examintro/examintro.component';
import { ExamdashComponent } from './components/examboard/examdash/examdash.component';
import { ExamresultComponent } from './components/examboard/examresult/examresult.component';
import { ErrorpageComponent } from './components/errorpage/errorpage.component';

const appRoutes: Routes =  [
  {path: '' , component : HomeComponent} ,
  {path: 'testboard' , component : TestboardComponent,
     children: [
        {path: '', component:ErrorpageComponent }, 
        {path: ':id', component:IntropageComponent }, 
        {path: 'testpage', component: MainpageComponent}, 
        {path: 'resultpage', component: ResultpageComponent}
     ]
  } ,
  {path: 'examboard' , component : TestboardComponent,
    children: [
       {path: '', component:ErrorpageComponent }, 
       {path: ':id', component:ExamintroComponent }, 
       {path: 'mainpage/:id', component: ExamdashComponent}, 
       {path: 'exam/resultpage', component: ExamresultComponent}
      ]
  } ,
  {
    path: 'uhome' , component : UhomeComponent,
    children: [
      {path: '', component: UdashboardComponent }, 
      {path: 'tutorials', component: TutorialsComponent}, 
      {path: 'questions', component: QuestionsComponent}, 
      {path: 'exams', component: ExamsComponent}, 
      {path: 'status', component: UstatusComponent}, 
    ]
  } , 
  {path: 'forgotpass' , component: FpasswordComponent } ,
  {path: 'features' , component: FaqComponent } ,
  {path: 'aboutus' , component: ContactComponent } ,
  {path: 'profile' , component : ServicesComponent },
  {path: 'e-examadmin/login' , component : AloginComponent },
  {
    path: 'e-examadmin/dash' , component : AhomeComponent,
    children: [
      {path: '', component: AdashboardComponent }, 
      {path: 'upload', component: UploadComponent} ,
      {path: 'extra', component: ExtraComponent}
    ]
  }
];

@NgModule({
  declarations: [
    AppComponent,
    NavbarComponent,
    HomeComponent,
    FooterComponent,
    ContactComponent,
    ServicesComponent,
    FaqComponent,
    UhomeComponent,
    RegisterComponent,
    LoginComponent,
    FpasswordComponent,
    UnavbarComponent,
    UdashboardComponent,
    TutorialsComponent,
    QuestionsComponent,
    ExamsComponent,
    UstatusComponent,
    AhomeComponent,
    AnavbarComponent,
    UploadComponent,
    AloginComponent,
    AdashboardComponent,
    ExtraComponent,
    TestboardComponent,
    IntropageComponent,
    MainpageComponent,
    ResultpageComponent,
    ExamboardComponent,
    ExamintroComponent,
    ExamdashComponent,
    ExamresultComponent,
    ErrorpageComponent
    ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    RouterModule.forRoot(appRoutes),
    ServiceWorkerModule.register('/ngsw-worker.js', { enabled: environment.production })
  ],
  providers: [
    AuthService,
    QuestionService,
    ExamService,
    ResultService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



