import { Injectable } from '@angular/core';
import {Http, Headers} from '@angular/http';
import 'rxjs/add/operator/map';
//import {tokenNotExpired} from 'angular-jwt';

@Injectable()
export class QuestionService {
  isDev: boolean;
  constructor(private http: Http) { }

//adding question to particular category
  addQuestion(question) {
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    let ep = 'http://localhost:3000/question/add';
    return this.http.post(ep, question, { headers: headers})
      .map(res => res.json());
  }

//getting category
  getCategory(){
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    let ep = 'http://localhost:3000/category/getcategories';
    return this.http.get(ep,{ headers: headers})
      .map(res => res.json());
  }

//getting topics
  getTopics(data){
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    let ep = 'http://localhost:3000/category/gettopics';
    return this.http.post(ep,data,{ headers: headers})
      .map(res => res.json());
  }

  //adding category
  addCategory(category){
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    let ep = 'http://localhost:3000/category/addcategory';
    return this.http.post(ep,category,{ headers: headers})
      .map(res => res.json());
  }

  //adding topic
  addTopic(topic){
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    let ep = 'http://localhost:3000/category/addtopic';
    return this.http.post(ep,topic,{ headers: headers})
      .map(res => res.json());
  }

  //adding exam name
  addExam(exam){
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    let ep = 'http://localhost:3000/exams/addexam';
    return this.http.post(ep,exam,{ headers: headers})
      .map(res => res.json());
  }

  //adding question to particular exam
  addExamQuestion(examQuestion){
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    let ep = 'http://localhost:3000/exams/addquestion';
    return this.http.post(ep,examQuestion,{ headers: headers})
      .map(res => res.json());
  }

  //getting all questions of particular exam
  getExamQuestion(exam){
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    let ep = 'http://localhost:3000/exams/getquestions';
    return this.http.post(ep,exam,{ headers: headers})
      .map(res => res.json());
  }
  
}



