import { Component, OnInit } from '@angular/core';
import {QuestionService} from '../../../services/question.service';
import {ExamService} from '../../../services/exam.service';

@Component({
  selector: 'app-extra',
  templateUrl: './extra.component.html',
  styleUrls: ['./extra.component.css']
})
export class ExtraComponent implements OnInit {

  topic;
  categories=[];
  category;
  successmsg; errormsg;
  cat;
  examname;

  constructor(
    private qs:QuestionService,
    private es:ExamService
  ) { }

  ngOnInit() {
     this.qs.getCategory().subscribe(data => {
      if (data.success) {
          this.categories = data.categories;
      } else {
        this.categories = [''];
      }
    });  
  }

  refreshTopics(){
    this.qs.getCategory().subscribe(data => {
      if (data.success) {
          this.categories = data.categories;
      } else {
        this.categories = [''];
      }
    });  
  }
  addCategory(){
    this.resetMsg();
    let data = {
     category: this.cat
   };
   if(data.category == undefined || data.category == '')
       this.errormsg = "please enter the category name";
   else{
    this.qs.addCategory(data).subscribe(data => {
      if (data.success) {
         this.successmsg = data.msg;
         this.cat= '';
      } else {
        this.errormsg = data.msg;
      }
    });  
   }   
  }

  addTopic(){
    this.resetMsg();
    let data = {
     category: this.category,
     topics:{name:this.topic}
   }
   if(data.category == undefined || data.category == '')
       this.errormsg = "please select the category name";
   else if(this.topic == undefined || this.topic == '')
      this.errormsg = "please enter the topic name";
   else{
    this.qs.addTopic(data).subscribe(data => {
      if (data.success) {
         this.successmsg = data.msg;
         this.topic = '';
      } else {
        this.errormsg = data.msg;
      }
    });  
   }   
  }

  addExam(){
     this.resetMsg();
     let data = {
      examname: this.examname
    }
    if(data.examname == undefined || data.examname == ''){
       this.errormsg = "please enter the exam name";
       return false;
    }
   else{
    this.es.addExam(data).subscribe(data => {
      if (data.success) {
         this.successmsg = data.msg;
         this.examname= '';
      } else {
        this.errormsg = data.msg;
      }
    }); 
   }
  }

  //=========(reseting messages)=============
  resetMsg(){
    this.errormsg = '';
    this.successmsg = '';
 }
}



