import { Injectable } from '@angular/core';
import {Http, Headers} from '@angular/http';
import 'rxjs/add/operator/map';
//import {tokenNotExpired} from 'angular-jwt';

@Injectable()
export class AuthService {
  authToken: any;
  user: any;
  isDev: boolean;
  admin:any;
  
  constructor(private http: Http) {
    this.isDev = false; // Change to false before deployment
  }

  registerUser(user) {
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    let ep = this.prepEndpoint('http://localhost:3000/users/register');
    return this.http.post(ep, user, { headers: headers})
      .map(res => res.json());
  }
  changePass(pass) {
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    let ep = this.prepEndpoint('http://localhost:3000/users/changepass');
    console.log(pass);
    return this.http.post(ep,pass, { headers: headers})
      .map(res => res.json());
  }
  authenticateUser(user) {
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    let ep = this.prepEndpoint('http://localhost:3000/users/authenticate');
    return this.http.post(ep, user, { headers: headers})
      .map(res => res.json());
  }

  authenticateAdmin(admin) {
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    let ep = this.prepEndpoint('http://localhost:3000/admin/authenticate');
    return this.http.post(ep, admin, { headers: headers})
      .map(res => res.json());
  }

  //for password reset 
  resetPass(email){
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    let ep = this.prepEndpoint('http://localhost:3000/users/resetpass');
    return this.http.post(ep, email, { headers: headers})
      .map(res => res.json());
  }
  getProfile() {
    let headers = new Headers();
    this.loadToken();
    headers.append('Authorization', this.authToken);
    headers.append('Content-Type', 'application/json');
    let ep = this.prepEndpoint('http://localhost:3000/users/profile');
    return this.http.get(ep, {headers: headers})
      .map(res => res.json());
  }

  storeUserData(token, user) {
   localStorage.setItem('id_token', token);
   localStorage.setItem('user', JSON.stringify(user));
    this.authToken = token;
    this.user = user;
  }

  storeAdminData(token, admin) {
    localStorage.setItem('id_token', token);
    localStorage.setItem('admin', JSON.stringify(admin));
     this.authToken = token;
     this.admin = admin;
   }

  loadToken() {
    const token = localStorage.getItem('id_token');
    this.authToken = token;
  }

  loggedIn() {
    return true;//tokenNotExpired('id_token');
  }

  logout() {
    this.authToken = null;
    this.user = null;
    localStorage.clear();
  }

  prepEndpoint(ep) {
    if (this.isDev) {
      return ep;
    } else {
      return ep;
    }
  }
}



