import { Component, OnInit } from '@angular/core';
import {AuthService} from '../../services/auth.service';
import { Router } from '@angular/router';
@Component({
  selector: 'app-alogin',
  templateUrl: './alogin.component.html',
  styleUrls: ['./alogin.component.css']
})
export class AloginComponent implements OnInit {

  errormsg:String;
  aname:String='';
  pass:String = '';
  constructor(
    private authService: AuthService,
    private router: Router
  ) { }

  ngOnInit() {

  }

  onLoginSubmit(){

    const admin = {
      aname: this.aname,
      password: this.pass
    }; 

    if(admin.aname == '' && admin.password == ''){
        this.aname='';
        this.pass='';
        this.errormsg = "Admin-ID and Password is required !";
        return;
    }
    else{
        this.authService.authenticateAdmin(admin).subscribe(data => {
        if (data.success) {
          this.authService.storeAdminData(data.token, data.admin);
          this.router.navigate(['/e-examadmin/dash']);
        } else {
          this.errormsg = data.msg;
        }
      });
    }
  }

  closeAll(){
    this.errormsg = '';
  }

}



