import { Component, OnInit } from '@angular/core';
import {QuestionService} from '../../../services/question.service';
import {ExamService} from '../../../services/exam.service';

@Component({
  selector: 'app-upload',
  templateUrl: './upload.component.html',
  styleUrls: ['./upload.component.css']
})
export class UploadComponent implements OnInit {

//================quesion upload============ //
question;
option1;option2;option3;option4;
answer;
categories = [] ;
category:String;
topic:String;
chapters;
Aptitute=[];
Resoning = [];
English = [];

//===========(message)======================//
success:String;
error:String;

//==============(Exam question variables)==============//
examQuesStat:boolean;
aptitutelen=5;
resoninglen=10;
englen=3;
genlen=24;
exams=[];
examname; //exam name
examcat;  //exam category

//==============(Exam question variables)==============//
  constructor(
    private qs:QuestionService,
    private es:ExamService,
  )
   {
    this.resetMsg();
    this.qs.getCategory().subscribe(data => {
      if (data.success) {
          this.categories = data.categories;
      } else {
        this.categories = [''];
      }
    });  
  }

  ngOnInit() {
    this.resetMsg();
    this.examQuesStat = false;
    this.es.getExams().subscribe(data => {
      if (data.success) {
          this.exams = data.data;
          this.examname = data.data[0].examname;
       } else {
        this.exams = [''];
      }
    }); 

  }

//===========(question upload methods)====================//
  changeCategory(value){
    this.resetMsg();
    this.chapters = [''];
    this.category = value;
    let catdata = {
    category : this.category
    }
    this.qs.getTopics(catdata).subscribe(data => {
      if (data.success) {
          this.chapters = data.data.topics;      
      } else {
      //  this.setError(data.msg);
        this.chapters = [''];
      }
    });  
  }

  //refreshing categories list
  refreshCategory(){
    this.resetMsg();
    this.qs.getCategory().subscribe(data => {
      if (data.success) {
          this.categories = data.categories;
      } else {
        this.categories = [''];
      }
    });  
  }
  getData(value){
    if(value == "Aptitute")
      return this.Aptitute;
    else if(value == "Resoning")  
           return this.Resoning;
    else if(value == "English")
       return this.English;
  }

  onQuesSubmit(){
    this.resetMsg();
    const question = {
      question : this.question,
      options : [this.option1,this.option2,this.option3,this.option4],
      answer:this.answer,
      category: this.category,
      topic: this.topic
    }
    if(this.topic == undefined || this.topic == ''){
      this.setError("please select the topic name");
      return false;
    }
    else if(question.question == undefined || question.answer == undefined){
      this.error = "please fill all the fileds ";
      return false;
    }
    else if(question.question == undefined || question.question == ''){
      this.error = "please fill the Question fileds ";
      return false;
    }
    else if(question.options[0] == undefined || question.options[1] == undefined || question.options[3] == undefined||question.options[3] == undefined){
              this.error = "please fill all the Options fileds ";
              return false;
    }
    else if(question.answer == undefined || question.answer == ''){
      this.error = "please fill  the Answer fileds ";
      return false;
    }
    else{
      this.qs.addQuestion(question).subscribe(data => {
       if (data.success) {
         this.success=data.msg;
         this.clearAll();
        } else {
         this.error = data.msg;
         return;
       }
     });
    }
  }
//===========(question upload methods)====================//


//===========(Exam question upload methods)======================//
  enableStat(){
      this.examQuesStat = true;
      this.populateQuestionNumber(); //populating number of questions
  }
  disableStat(){
    this.examQuesStat = false;
    this.resetMsg();
  }

  changeExam(value){
      this.examname = value;
      this.populateQuestionNumber();
  }

  addExamQuestion(){ ///question adding method
      this.resetMsg();
      const examQuestion = {
        examname:this.examname,
        questions:{
        question : this.question,
        options : [this.option1,this.option2,this.option3,this.option4],
        answer:this.answer,
        category: this.examcat
      }
    }

    if(examQuestion.examname == undefined || examQuestion.examname == ''){
           this.error = "Please select the exam name";
           return false;
    }
    else if(this.examcat == undefined || this.examcat == ''){
            this.error = "Please select the category";
            return false;
        }
        else if(this.question == undefined || this.question == undefined){
          this.error = "please fill all the fileds ";
          return false;
        }
        else if(this.option1 == undefined || this.option2 == undefined || this.option3 == undefined||this.option4 == undefined){
                  this.error = "please fill all the Options fileds ";
                  return false;
        }
        else if(this.answer == undefined || this.answer == ''){
          this.error = "please fill  the Answer fileds ";
          return false;
        }  
    else{
      this.es.addExamQuestion(examQuestion).subscribe(data => {
       if (data.success) {
         this.success=data.msg;
         this.populateQuestionNumber();
         this.clearAll();
        } else {
         this.error = data.msg;
         return;
       }
     });
    }

  }

//number of question uploaded to particular exam
    populateQuestionNumber(){
      console.log(this.examname);
      const ename = {
        examname:this.examname
      }
      this.es.getExamQuestionStat(ename).subscribe(data => {
        if (data.success) {
          console.log(data);
           this.aptitutelen=data.data.aptitute;
           this.resoninglen = data.data.resoning;
           this.englen = data.data.english;
           this.genlen = data.data.genral;
        }
      }); 
    }
 //===========(Exam question upload methods)======================//

  clearAll(){
    this.question = '';
    this.option1 = '';
    this.option2 = '';
    this.option3 = '';
    this.option4 = '';
    this.answer = '';
    this.topic = null;
    }

    resetMsg(){
      this.error = '';
      this.success = '';
    }

    setError(msg){
          this.error = msg;
    }
    setSuccess(msg){
      this.success = msg;
    }
}



